:root {
  --menu-bg-color: #c6d4e1;
  --text-color: #333;
  --text-color-alt: #545454;
  --text-color-light: #999;
  --border-color: #ccc;
  --black: #000;
  --white: #fff;
}

.font-face-ca {
  font-family: 'CasablancaAntique';
}

.hidden {
  display: none;
}

#maps-background {
  background-color: #111;
}

#maps-background ul {
  margin: 0;
}

#banner-image {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 0;
  padding-top: 20%;
}

.header {
  background-color: var(--menu-bg-color);
  position: sticky;
  top: 0;
  z-index: 2;
  width: 100%;
}

.home {
  background-image: url('../assets/banners/bfg.png');
}

.background {
  background-image: url('../assets/banners/battlefleet.png');
}

.events {
  background-image: url('../assets/banners/artillery.png');
  background-position: 0 -200px;
}

.factions {
  background-image: url('../assets/banners/tank.png');
}

.characters {
  background-image: url('../assets/banners/big-mek.png');
}

.menu-nav {
  width: fit-content;
  background-color: var(--menu-bg-color);
  text-transform: uppercase;
  font-size: .8rem;
  display: flex;
  justify-content: space-between;
  z-index: 2;
}

.side-menu {
  display: none;
}

.hamb {
  display: none;
}

.navigation {
  display: flex;
  margin: 0;
  padding: 0.75rem 0.375rem 0.75rem 0.375rem;
  user-select: none;
}

.navigation li {
  padding: 0;
  color: var(--text-color-alt);
}

.navigation li:not(:first-child):before {
  content: '|';
  color: #a4bacf;
  margin-right: 1.25rem;
}

.navigation li a {
  margin-right: 20px;
  padding: 7.5px 10px;
  text-decoration: none;
  text-transform: inherit;
}

.menu-active {
  background: rgba(25, 25, 25, 0.2);
  color: var(--white);
}

@media screen and (max-width: 768px) {
  .menu-nav {
    width: 100%;
    height: auto;
    position: fixed;
    overflow: hidden;
    background-color: var(--menu-bg-color);
  }

  .menu-nav {
    max-height: 0;
    transition: max-height .5s ease-out;
  }

  .menu-nav > .navigation {
    display: block;
  }

  .menu-nav > .navigation li:not(:first-child):before {
  content: inherit;
  color: inherit;
  margin-right: inherit;
}

  .navigation li {
    margin-bottom: 1.25rem;
  }

  .home-img {
    display: none;
  }
  
  .hamb {
    display: inline-block;
    position: relative;
    cursor: pointer;
    padding: 2rem 1rem;
  }

  .hamb-line {
    background: var(--text-color-alt);
    display: block;
    height: 0.125rem;
    position: relative;
    width: 1.5rem;
  }

  .hamb-line::before,
  .hamb-line::after {
    background: var(--text-color-alt);
    content: '';
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%;
  }
  .hamb-line::before{
    top: 5px;
  }
  .hamb-line::after{
    top: -5px;
  }

  /* Toggle menu icon */
  .side-menu:checked ~ .menu-nav {
    max-height: 100%;
  }
  .side-menu:checked ~ .hamb .hamb-line {
    background: transparent;
  }
  .side-menu:checked ~ .hamb .hamb-line::before {
    transform: rotate(-45deg);
    top: 0;
  }
  .side-menu:checked ~ .hamb .hamb-line::after {
    transform: rotate(45deg);
    top: 0;
  }

  #banner-image {
    display: none;
  }
}

article {
  padding: 1.25rem;
}

article p {
  padding-bottom: 1rem;
}

article ul {
  margin: 0;
}

.timeline li {
  padding: 0.5rem;
}

.symbol {
  height: 6rem;
  margin: auto;
  opacity: 0.75;
  padding-top: 2rem;
}

.sub-nav {
  margin: auto;
}

.sub-nav ul li {
  color: var(--text-color-light);
  margin-right: 1.25rem;
  font-family: 'CasablancaAntique';
  cursor: pointer;
}

/* Modal */
.full-screen {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.modal-layer {
  transform: translateZ(0);
  background: none;
  z-index: 10;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: translateZ(0);
  opacity: 0.85;
  background: var(--black);
}

.modal-center {
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-height: 0;
}

.modal-body {
  height: auto;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.image-wrapper {
  max-width: 600px;
  max-height: 600px;
  display: block;
  position: relative;
}

.modal-center a {
  text-decoration: none;
  color: var(--text-color-light);
  font-size: 0.8rem;
  font-weight: 500;
  transition: opacity .15s ease;
  opacity: 0.5;
  line-height: 2rem;
}

.modal-center a:hover {
  text-decoration: underline;
  color: var(--white);
}

a[role="button"] {
  cursor: pointer;
}