.char-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.char-li {
  margin: 0.75rem;
}

.character-section {
  border: 2px solid var(--border-color);
  display: flex;
  flex-direction: column;
  border-radius: 0.75rem;
  padding: 0.75rem 0.375rem 0.75rem 0.375rem;
  overflow-wrap: break-word;
  cursor: pointer;
  height: auto;
}

.char-card {
  display: flex;
}

.char-card img {
  max-width: 192px;
  max-height: 192px;
  align-self: center;
}

.char-summary {
  width: 100%;
  position: relative;
  overflow: hidden;
  white-space: normal;
  padding: 0.375rem 0.75rem;
}

.char-summary.compress {
  max-height: 192px;
}

.background-emblem {
  position: absolute;
  top: 96px;
  right: 0;
  transform: translateY(-50%);
  opacity: 0.1;
}

@media screen and (max-width: 768px) {
  .char-list {
    display: block;
  }

  .char-card {
    display: block;
  }

  .char-card aside {
    min-width: 0;
  }

  .char-card img {
    max-width: 100%;
  }

  .background-emblem {
    display: none;
  }
}

.char-stats {
  display: none;
  padding: 0 12px;
}

.char-stats.expand {
  display: block;
}

.char-desc {
  display: none;
  padding: 0.75rem 0 0.375rem;
}

.char-desc.expand {
  display: inline-block;
}

.campaign-info {
  padding: 0.75rem 0;
}

table {
  border-collapse: collapse;
  border: none;
  margin-bottom: 2.25rem;
}

table:last-child {
  margin-bottom: 0;
}

table tr {
  border-bottom: 1px solid #111;
}

table th, td {
  border-right: 1px solid #111;
  padding: 2px 6px;
  text-align: center;
}

table tr:last-child, th:last-child, td:last-child {
  border: none;
}

.weapons-table td {
  font-size: 0.8rem;
  text-align: left;
}
