.p-quote-card {
  max-width: 30vw;
  border: 0.25rem;
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin: auto;
  background-color: rgba(25, 25, 25, 0.4);
  color: var(--white);
  margin-bottom: 2rem;
  clear: 
  both;
}
@media screen and (max-width: 1200px) {
  .p-quote-card {
    max-width: 50vw;
  }
}

@media screen and (max-width: 1024px) {
  .p-quote-card {
    max-width: 66vw;
  }
}

@media screen and (max-width: 768px) {
  .p-quote-card {
    max-width: 80vw;
  }
}

.p-quote {
  border-left: 0.25rem solid var(--white);
  font-style: italic;
  margin: 0.5rem;
}

.p-quote p {
  font-style: italic;
  padding: 0.25rem;
}

div.thumb {
  width: auto;
  background-color: transparent;
  margin-bottom: 0.5rem;
}

div.tleft {
  margin: 0.5rem 1.4rem 1.3rem 0;
  clear: left;
  float: left;
}

div.tright {
  margin: 0.5rem 0 1.3rem 1.4rem;
  clear: right;
  float: right;
}

div.tnone {
  display: inline-block;
  margin: auto;
  clear: both;
}

@media screen and (max-width: 768px) {
  div.thumb {
    display: inline-block;
    clear: both;
  }

  div.tleft {
    margin-left: 50%;
    transform: translateX(-50%);
  }

  div.tright {
    margin-right: 50%;
    transform: translateX(50%);
  }
}

div.thumbinner {
  border: 1px solid var(--border-color);
  background-color: var(--white);
  font-size: 94%;
  padding: 3px;
  text-align: center;
  overflow: hidden;
  min-width: 100px;
}

.thumbimage {
  border: 1px solid var(--border-color);
}

.thumbcaption {
  border: 0;
  font-size: 94%;
  line-height: 1.4rem;
  padding: 3px;
}

ul.gallery {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  clear: both;
  margin: 2px;
  padding: 2px;
}

li.gallerybox {
  vertical-align: top;
  display: inline-block;
  width: 300px;
  margin: 0 0.5em 0.5em 0;
}

li.gallerybox div.thumb {
  border: 1px solid #c8ccd1;
  background-color: #f8f9fa;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
  width: 300px;
  padding: 15px;
}

.gallery-img-wrapper {
  display: flex;
  align-items: center;
  margin: 15px;
}

li.gallerybox div.thumb img {
  display: block;
  margin: 0 auto;
  object-fit: cover;
}

.gallerytext {
  overflow: hidden;
  font-size: calc(1em * 0.875);
  padding: 2px 4px;
  word-wrap: break-word;
  line-height: 1.4;
}

.spaced-head {
  margin: 0.5em 0;
}

.orkish {
  color: darkgreen;
  font-family: 'HondaRegular' !important;
}

.ork-heading {
  color: darkgreen;
  font-size: 2em;
  font-weight: bolder;
  margin: 0.5em 0;
  font-family: 'HondaRegular' !important;
}

.ork-speech {
  color: darkgreen;
  font-size: 1.1em;
  font-weight: bold;
}

.grot-speech {
  color: #8A9A5B;
  font-size: 0.9em;
  font-weight: bold;
}

.paragraphs {
  margin-bottom: 0.3em;
}

.end-of-section {
  margin-bottom: 1.3em;
}