.event-section {
  margin: 1.5rem;
  display: flex;
  border-radius: 0.375rem;
  padding: 0.75rem 0.375rem 0.75rem 0.375rem;
}

.event-record {
  cursor: pointer;
  border: 2px solid var(--border-color);
  border-radius: 0.75rem;
}

.event-section img {
  max-width: 192px;
  max-height: 160px;
  align-self: center;
}

@media screen and (max-width: 768px) {
  .event-section {
    display: block;
  }

  .event-section img {
    max-width: 100%;
  }
}

.event-filters {
  color: var(--text-color-light);
  font-family: 'CasablancaAntique';
}

.filter-options {
  display: flex;
}

.filter-option {
  padding-right: 1rem;
}

.filter-checkbox {
  accent-color: var(--text-color-alt)
}

.arrow {
  border: solid var(--text-color-light);
  border-width: 0 1px 1px 0;
  display: inline-block;
  padding: 3px;
  margin: 0 0 0.1em 0.5rem;
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.event-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.army-lists {
  display: flex;
  justify-content: space-between;
  width: 66vw;
  margin-bottom: 1.5rem;
  border: 2px solid var(--border-color);
  border-radius: 0.75rem;
}

.army-list {
  min-height: 100%;
  padding: 1rem;
  width: 100%;
  text-align: center;
}

.army-list.attacker {
  border-right: 1px solid var(--border-color);
}

.army-list.defender {
  border-left: 1px solid var(--border-color);
}

.event-summary {
  width: 80vw;
}

.event-summary > aside {
  padding: 0.5rem 0;
}

.event-summary > aside > p {
  padding: 0.5rem;
}

img.event-main {
  max-height: 600px;
}

img.event-faction {
  max-height: 200px;
}

.event-header > .thumb {
  margin-bottom: 1.5rem;
}

.army-list > .thumb {
  margin-bottom: 1rem;
}

.event-quote-card {
  max-width: 30vw;
  border: 0.25rem;
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin: auto;
  background-color: rgba(25, 25, 25, 0.4);
  color: var(--white);
  margin-bottom: 2rem;
  clear: both;
}

@media screen and (max-width: 1200px) {
  .army-lists {
    width: inherit;
  }

  .event-quote-card {
    max-width: 50vw;
  }
}

@media screen and (max-width: 1024px) {
  .army-lists {
    width: inherit;
  }

  .event-quote-card {
    max-width: 66vw;
  }
}

@media screen and (max-width: 768px) {
  .army-lists {
    width: inherit;
  }

  .event-quote-card {
    max-width: 80vw;
  }
}

.event-quote {
  border-left: 0.25rem solid var(--white);
  font-style: italic;
  margin: 0.5rem;
}

.event-quote p {
  font-style: italic;
  padding: 0.25rem;
}

div.thumb {
  width: auto;
  background-color: transparent;
  margin-bottom: 0.5rem;
}

div.tleft {
  margin: 0.5rem 1.4rem 1.3rem 0;
  clear: left;
  float: left;
}

div.tright {
  margin: 0.5rem 0 1.3rem 1.4rem;
  clear: right;
  float: right;
}

div.tnone {
  display: inline-block;
  margin: auto;
  clear: both;
}

@media screen and (max-width: 768px) {
  div.thumb {
    display: inline-block;
    clear: both;
  }

  div.tleft {
    margin-left: 50%;
    transform: translateX(-50%);
  }

  div.tright {
    margin-right: 50%;
    transform: translateX(50%);
  }
}

div.thumbinner {
  border: 1px solid var(--border-color);
  background-color: var(--white);
  font-size: 94%;
  padding: 3px;
  text-align: center;
  overflow: hidden;
  min-width: 100px;
}

.thumbimage {
  border: 1px solid var(--border-color);
}

.thumbcaption {
  border: 0;
  font-size: 94%;
  line-height: 1.4rem;
  padding: 3px;
}

ul.gallery {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  clear: both;
  margin: 2px;
  padding: 2px;
}

li.gallerybox {
  vertical-align: top;
  display: inline-block;
}

li.gallerybox div.thumb {
  border: 1px solid #c8ccd1;
  background-color: #f8f9fa;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
  width: 300px;
  padding: 15px;
}

.gallery-img-wrapper {
  display: flex;
  align-items: center;
  margin: 15px;
}

li.gallerybox div.thumb img {
  display: block;
  margin: 0 auto;
  object-fit: cover;
}

.gallerytext {
  overflow: hidden;
  font-size: calc(1em * 0.875);
  padding: 2px 4px;
  word-wrap: break-word;
  line-height: 1.4;
}