.faction-section {
  margin: 1.5rem;
  display: flex;
  border-radius: 0.375rem;
  padding: 0.75rem 0.375rem 0.75rem 0.375rem;
}

.faction-section aside {
  min-width: 192px;
  display: flex;
  justify-content: center;
}

.faction-section img {
  max-width: 192px;
  max-height: 160px;
  align-self: center;
}

.faction-section article {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.faction-section p {
  height: 100%;
}

@media screen and (max-width: 768px) {
  .faction-section {
    display: block;
  }

  .faction-section aside {
    min-width: 0;
  }

  .faction-section img {
    max-width: 100%;
  }
}

.faction-page {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.faction-summary {
  width: 80vw;
}

.faction-summary > aside {
  padding: 0.5rem 0;
}

.faction-summary > aside > p {
  padding: 0.5rem;
}

.end-symbol {
  max-width: 235px;
  opacity: 0.6;
  margin-bottom: 2em;
}

.orkish {
  color: darkgreen;
  font-family: 'HondaRegular' !important;
}